$box-size: border-box;

.skill_container:hover {
  -webkit-transform: translateY(0.5rem);
  -webkit-transition: -webkit-transform 0.2s ease;
}

.skill_container {
  -webkit-box-sizing: $box-size;
  flex-shrink: 0;
  width: 17rem;
  padding: 1.5rem;
  margin: 0 auto 2rem;
  &.space {
    margin-right: 1.5rem;
  }
  border-radius: 1rem;
  background-color: #fff;
  -webkit-box-shadow: 1rem 1rem 1rem 0 rgba(68, 68, 68, 0.2);
}

.skill_container_title {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-bottom: 1.5rem;
  margin-bottom: 1.5rem;
  border-bottom: 1px solid #ccc;
  font-weight: 700;
  font-size: 2.5rem;
  color: #f4623a;
  cursor: default;
}

.skill_container_img {
  display: block;
  &.flex {
    display: flex;
  }
  width: 100%;
}

@media (max-width: 650px) {
  .skill_container {
    width: 9rem;
    padding: 0.5rem;
    margin: 0 auto 1rem;
    &.space {
      margin-right: 1rem;
    }
  }

  .skill_container_title {
    font-size: 1.3rem;
    padding-bottom: 0.8rem;
    margin-bottom: 1rem;
  }
}
